<template>
    <div class="item_info">
        <div class="header"></div>
        <div class="body">
            <div class="sprite_container">
                <img :src="`https://web.simple-mmo.com${item.image}`" alt="Server down">
            </div>
            <div class="info_container">
                <div class="">
                    <div :style="{color: rarity[item.Rarity].color }" class="name">{{ item.Name }}</div>
                    <div class="level">Level {{ item.Level }} {{ item.Type }} [<div class="id">{{ item.ItemID }}</div>]</div>
                    <div :style="{color: rarity[item.Rarity].color }" class="rarity">{{ item.Rarity }}</div>
                </div>
                <div class="">
                    <div v-if="item.Stat1 != '' && item.Stat1 != 'none'">{{item['Stat1-Value']}} {{fixStats(item.Stat1)}} </div>
                    <div v-if="item.Stat2 != '' && item.Stat2 != 'none'">{{item['Stat2-Value']}} {{fixStats(item.Stat2)}} </div>
                    <div v-if="item.Stat3 != '' && item.Stat3 != 'none'">{{item['Stat3-Value']}} {{fixStats(item.Stat3)}} </div>
                </div>
            </div>
            <div class="action_container"></div>
        </div>
        <div class="footer"></div>
    </div>
</template>

<script>
    export default {
        name: "ItemInfo",
        props: ["item", "rarity"],
        methods:{
            fixStats(stat){
                if(stat == "str") return "Strength";
                if(stat == "def") return "Defence";
                if(stat == "dex") return "Dexterity";
                if(stat == "energy") return "Energy";
                if(stat == "health") return "Health";
                return stat;
            }
        },
        created(){
            console.log(this.item);
        }
    }
</script>

<style scoped>
.info_container{
    margin-left: 0.5rem;
    display: grid;
    grid-template-columns: auto 8rem;
    column-gap: 0.5rem;
    width: 80%;
}
.sprite_container{
    width: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sprite_container img{
    height: auto;
    max-width: 32px;
}

.body{
    display: flex;
}
.item_info{
    color: var(--text);
    padding: 1rem;
    background: #141414;
    border-radius: 0.5rem;
    font-size: 0.9rem;
}
.name{
    user-select: all;
}
.id{
    user-select: all;
}
.level{
    display: flex;
}
</style>