<template>
  <div class="container">
    <BackButton pageTitle="Open Chests" @goBack="goBack" />
    <div class="content">
      <div class="grid">
        <div class="item">
            <div class="img_container">
                <img src="../assets/images/chest.png" alt="">
            </div>
            <div class="title">
                Bronze Chest
            </div>

            <div class="sub-title">Open</div>
            <div class="button-grid">
                <button :disabled="this.player.keys.bronze < 1" @click="openKey('bronze', 1)">1</button>
                <button :disabled="this.player.keys.bronze < 5" @click="openKey('bronze', 5)">5</button>
                <button :disabled="this.player.keys.bronze < 10" @click="openKey('bronze', 10)">10</button>
            </div>
        </div>
        <div class="item">
            <div class="img_container">
                <img src="../assets/images/chest.png" alt="">
            </div>
            <div class="title">
                Silver Chest
            </div>
            <div class="sub-title">Open</div>
            <div class="button-grid">
                <button :disabled="this.player.keys.silver < 1" @click="openKey('silver', 1)">1</button>
                <button :disabled="this.player.keys.silver < 5" @click="openKey('silver', 5)">5</button>
                <button :disabled="this.player.keys.silver < 10" @click="openKey('silver', 10)">10</button>
            </div>
        </div>
        <div class="item">
            <div class="img_container">
                <img src="../assets/images/chest.png" alt="">
            </div>
            <div class="title">
                Gold Chest
            </div>
            <div class="sub-title">Open</div>
            <div class="button-grid">
                <button :disabled="this.player.keys.gold < 1" @click="openKey('gold', 1)">1</button>
                <button :disabled="this.player.keys.gold < 5" @click="openKey('gold', 5)">5</button>
                <button :disabled="this.player.keys.gold < 10" @click="openKey('gold', 10)">10</button>
            </div>
        </div>
      </div>
      <div class="new-items" v-if="showLoot">
        <div class="cat-title">Dropped:</div>
        <div class="drop">
          <div @click="itemInfo(item)" v-for="item in player.newest_items" :key="item.unique_id" class="new-item" :style="{color: rarity[item.Rarity].color }">
            {{ item.Name }}
          </div>
        </div>
        <div class="itemInfo">
          <ItemInfo :rarity="rarity" v-if="showItem" :item="item"  />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BackButton from "./BackButton.vue";
import ItemInfo from "./ItemInfo.vue";

export default {
  name: "OpenChests",
  components: {
    BackButton,
    ItemInfo
  },
  props: ["player", "rarity"],
  data(){
    return {
      showLoot: false,
      showItem: false,
      item: {}
    }
  },
  methods: {
    goBack() {
      this.$emit("goBack");
    },
    openKey(keyName, amount) {
      this.item = {}
      this.showItem = false;
      this.$emit("openKey", [keyName, amount]);
      this.showLoot = true;
      console.log(this.player.newest_items)
    },
    itemInfo(item){
      if(this.item == item){
        this.item = {}
        return this.showItem = false;
      }
      this.item = item;
      this.showItem = true;
      console.log(item);
    }
  },
  created(){
    console.log(this.rarity);
  }
};
</script>

<style scoped>
.cat-title{
  color: var(--text);
  font-weight: 600;
  font-size: 1.1rem;
}
.new-items{
    width: 100%;
    margin-top: 1rem;
    cursor: pointer;
}

.itemInfo{
  margin-top: 1rem;
}

.drop{
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.new-item{
  margin-top: 0.2rem;
  font-weight: 400;
}


.container {
  height: 100%;
}
.content {
  height: calc(100% - 3rem);
  width: 100%;
}
.grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  width: 100%;
  column-gap: 0.5rem;
}
.item{
    display: flex;
    flex-direction: column;
    padding: 3rem 0rem;
    background: rgb(49, 49, 49);
    align-items: center;
    border-radius: 0.5rem;
}

.title{
    color: var(--text);
    font-weight: 600;
    margin-top: 0.5rem;
}

.img_container{
    height: 2rem;
    width: 2rem;
}

.item .img_container img{
    height: 100%;
}

.sub-title{
    font-size: 0.9rem;
    margin-top: 2rem;
    color: rgb(177, 177, 177);
    font-weight: 500;
}
.button-grid{
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    margin-top: 0.5rem;
    column-gap: 0.5rem;
}

.button-grid button{
    padding: .5rem;
    border: 2px solid rgb(30, 157, 216);
    border-radius: 0.2rem;
    background: rgb(43, 43, 43);
    color: white;
    cursor: pointer;
}

.button-grid button:hover{
    background: rgb(26, 26, 26);
}

.button-grid button:disabled{
    pointer-events: none;
    opacity: 60%;
}


</style>